.navbar{
    border-bottom: 2px solid black;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* margin-bottom: 100px; */
    height: 60px;
    top: 0;
    position: fixed;
    width: 100vw;
    /* background-color: white; */
    background: transparent;
    backdrop-filter: blur(10px);
    z-index: 100;
    flex-wrap: auto;
}
.ul{
    display: flex;
    list-style: none; 
}

.li{
    margin-right: 30px;
    display: flex;
    align-items: center;
    border: none;
    background: none;
    font-size: 15px;
    padding: 20px 0px;
}

.buttonlogin{
    border: 1px solid black;
    border-radius: 10px;
    padding: 10px;
    margin-right: 10px;
}

.buttonsign{
    border: 1px solid black;
    border-radius: 10px;
    padding: 10px;
    margin-right: 10px;
    background-color: rgb(110, 217, 219);
}

.name{
    color: rgb(8, 112, 114);
    font-weight: 700;
    font-size: 20px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.hove{
    display: none;
}

.hoverli:hover > .hove{
    display: block;
    position: absolute;
    margin-top: 60px;
    width: 100vw;
    left: 0;
    top: 0;
    height: fit-content;
    background-color: white;
    border-bottom-style: ridge;
    box-shadow: 0px 0px 3px black; 
    z-index: 1;
}


.hover{
    display: flex;
    position:fixed;
    margin-top: 122px;
    /* border-top: 1px solid black; */
    width: 100vw;
    height: 60px;
    margin-right: 100px;
}

.liicon{
    width: 0.5px;
    margin-left: 2px;
}

.hoverli:hover > .liicon{
    animation: anurag;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
}

@keyframes anurag {
    from{
        rotate: 0deg;
    }
    to{
        rotate: 180deg;
    }
}


.hovefirst{
    width: fit-content;
}

.whycarehover{
    display: flex;
    margin-left: 40px;
    margin-top: 40px;
    width: 600px;
    height: 300px;
}

