.search{
    display: flex;
    justify-content: center;
    margin-top: 20px;
    flex-wrap: auto;
    flex-shrink: 1;
}

.searchmain{
    flex-shrink: 1;
    border: 1px solid black;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
    padding: 10px;
}


.Started{
    border-radius: 10px;
    border: 1px solid black;
    padding: 10px;
    background-color: rgb(179, 180, 181);
    /* background-color: rgb(87, 211, 239); */
}

.searchinput{
    width: 300px;
    flex-shrink: auto;
    padding: 10px;
    font-size: large;
    border: 1px solid black;
    border: none;
    background-color: transparent;
}

.searchinput::placeholder{
    background-color: transparent;
}

.searchmain input{
    outline: none;
    flex-shrink: 2;
}

.heading{
    font-size: 60px;
    display: flex;
    justify-content: center;
    margin-top: 90px;
}

.cat{
    margin-top: 40px;
    height: max-content;
    margin-left: 60px;
}

.cat ul{
    list-style: none;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    overflow: auto;
}

.catli{
    display: flex;
    align-items: center;
    border: 2px solid black;
    margin-right: 20px;
    font-size: 20px;
    padding: 10px;
    border-radius: 10px;
    width: fit-content;
    margin: 10px 10px;
    color: black;
}

.icon{
    margin-right: 5px;
}

footer{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
}

p{
    font-size: 30px;
}


.hlist{
    border: 1px solid black;
    width: 90vw;
    margin: auto;
    border-radius: 15px;
    flex-wrap: auto;
    margin-bottom: 20px;
}

.dis{
    padding: 20px;
}

.disheading{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 25px;
    color: rgb(12, 128, 146);
    flex-wrap: auto;
}

.disupper{
    /* border: 1px solid black; */
    height: 150px;
    display: flex;
    border-radius: 15px;
    flex-wrap: auto;
    flex-wrap: auto;
}

.disphoto{
    /* border: 1px solid black; */
    width: 200px;
    height: 150px;
    margin-right: 30px;
    border-radius: 15px;
}

.disphoto img{
    width: 200px;
    height: 150px;
    border-radius: 15px;
}

.disli{
    list-style: none;
}

.location{
    list-style: none;
    font-size: 20px;
    margin-top: 10px;
    color: rgba(46, 46, 46, 0.775);
}

.disend{
    padding: 15px 0px;
    width: fit-content;
    flex-wrap: auto;
}

.hname{
    list-style: none;
    width: 300px;
    flex-wrap: auto;
    font-size: 24px;
}

.hdetail{
    border: black 1px solid;
    margin-left: 50px;
    height: fit-content;
    width: 30vw;
    display: flex;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    list-style: none;
    margin-top: 20px;
    padding: 10px;
    border-radius: 15px;
    background-color: rgb(231, 249, 211);
    justify-content: space-evenly;
}

.insidehdetail{
    border: 1px solid black;
    font-size: 15px; 
    width: fit-content;
    padding: 5px 10px;
    border-radius: 5px;
    margin-bottom: 5px;
    margin-top: 5px;
    background-color: white;
}